import React from 'react';
import './Navigation.css';
import '../../index.css'
import { HashLink as Link } from 'react-router-hash-link';

const Header = () => {
    
    return (
        <header>
            <Link to='/'></Link>
            <Link to="/#calc"> </Link>
            <Link to="/#work_types"> </Link>
            <Link to="/#call"> </Link>
            <Link to="/#about_us"> </Link>
            <Link to="/#contacts"></Link>
        </header>
    );
};
export default Header;