import React from "react";
import "../../index.css";
import "../Main_sec2/Main_sec2"

export default function Select({text,name}){
    return(
        <>
        <label htmlFor={name}>{text}</label>
        <select name={name} id={name} className="control">
            <option value="">  </option>
            {name=="form" &&
            <>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=" "> </option>
            </>}
            {name=="work" &&
            <> 
                <option value=""></option>
                <option value=""></option>
                <option value="+">+</option>
                <option value=" "> </option>
                <option value="  ">  </option>
            </>}
            {name=="type" &&
            <>
                <option value=""></option>
                <option value=""></option>
                <option value=" "> </option>
                <option value=" "> </option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
            </>}
        </select>
        </>
    )
}